<template>
    <div class="echart" ref="mapRef" id="map"></div>
</template>

<script>
    import chinaJson from './china.json'
    import resizeMixins from '@/utils/resizeMixins'
    import 'echarts/lib/chart/scatter'
    import gdp2020 from './gdp_2020.json'
    import lines_data from './lines_data.json'
    import link_coordinate from './link_coordinate_new.json'

    var cityName = ''
    export default {
        name: "index",
        mixins: [resizeMixins],
        data(){
            return{
                data: gdp2020,
                chart:null,
            }
        },
        watch: {
            options: {
                handler (options) {
                    // trueechart
                    this.chart.setOption(options, true)
                },
                deep: true
            }
        },
        mounted() {
            this.initChart();
            // this.handleMapRandomSelect(); //
        },
        beforeDestroy() {
            if (this.timer) {clearInterval(this.intervalId);}
        },
        methods: {
            initChart() {
                let that = this;
                this.chart = this.$echarts.init(document.getElementById('map'), 'shine');
                this.$echarts.registerMap("china", chinaJson);
                var lineseries = [];
                lineseries.push({
                    name: cityName,
                    type: 'lines',
                    zlevel: 1,
                    show: true,
                    effect: {
                        show: true,
                        period: 4, // 
                        symbol: 'arrow',
                        trailLength: 0.6, //  0  1  0.2
                        color: '#fff',
                        // symbol: planePath, // 
                        symbolSize: 6
                    },
                    tooltip: {
                        show: true, // 
                        trigger: 'item', //   'item' 'axis''none'
                        // backgroundColor: 'rgba(50,50,50,0.7)', // 
                        // borderColor: '#fff', // 
                        borderWidth: 0, // 
                        // padding: 5, // 
                        // textStyle: { // 
                        //     color: '#fff',
                        //     fontStyle: 'normal',
                        //     fontWeight: 'normal',
                        //     fontFamily: 'sans-serif',
                        //     fontSize: 14,
                        // },
                        // extraCssText: 'box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);', //  css 
                        confine: false, //  tooltip 
                        // formatter: '{b}  {c}'
                        formatter: function(arg) {
                            return ''+arg.data.fromName + '' + arg.data.toName + '<br/>:<br/>'+ arg.data.value
                        }
                    },
                    lineStyle: {
                        normal: {
                            color: function(arg){
                                var fromName = arg.data.fromName
                                if (fromName == cityName){
                                    return '#CD5C5C'
                                }
                                return '#3f4b51'},
                            width:  2.5,
                            opacity: 0.2,
                            curveness: 0.2
                        }
                    },
                    data: lines_data[cityName]
                },
                {
                    name: cityName+'2',
                    type: 'effectScatter',      // 
                    coordinateSystem: 'geo',    // 
                    zlevel: 2,
                    rippleEffect: {
                        brushType: 'stroke'     // 
                    },
                    label: {
                        normal: {
                            show: true,
                            position: 'left',  // 
                            formatter: '{b}'    // b
                        }
                    },
                    symbolSize: function (data) {
                        if(data[2]==1){
                            return 6
                        }
                        return 3;      // 
                    },
                    itemStyle: {
                        normal: {
                            color: function (val) {
                                if(val.data.name == cityName){
                                    return '#FFA500'
                                }
                                return 'MidnightBlue'}
                        }
                    },
                    // item[1] ==> BJData[[{name:''}, {name:'',value:95}],...]
                    data: link_coordinate[cityName]
                },
                );
                // 
                let options = {
                    title: {
                        text: '2020GDP',
                        subtext: '',
                        textStyle:{
                            color:'#377ee5'
                        },
                        subtextStyle:{
                            color:'#c7d5d9'
                        }
                    },
                    tooltip: {
                        textStyle: {
                            fontSize: '14px',
                        },
                        formatter: function(data){
                            if(!isNaN(data.value)){
                                return data.name+"<br/>"+data.value+"";
                            }else{
                                return data.name
                            }
                        }
                    },
                    geo:{
                        type: "map",
                        roam: false, //
                        map: "china",
                        itemStyle: {
                            areaColor: "LightGrey",
                            borderColor: "#739af5",
                            borderWidth: 1
                        },
                        emphasis: {
                            label: {
                                show: false
                            },
                            itemStyle: {
                                areaColor: "#f59a05"
                            }
                        }
                    },
                    layoutCenter: ['50%', '50%'],//
	                layoutSize:'100%',//
                    visualMap: [
                    {
                        orient: 'horizontal',
                        min: 1900,
                        max: 120000,
                        text: ['GDP()'],
                        realtime: false,
                        calculable: true,
                        bottom:"0%",                              //,'20%'
                        inRange: {
                            color: ['Azure', '#1f8bcb']
                        },
                        textStyle:{
                            color:'#000000'
                        },
                    }],
                    series: [
                        {
                            name:'2020GDP',
                            type: "map",
                            // roam: true,
                            map: "china",   
                            geoIndex: 0,
                            itemStyle: {
                                areaColor: "#1f8bcb",
                                borderColor: "#739af5",
                                borderWidth: 1
                            },
                            emphasis: {
                                label: {
                                    show: false
                                },
                                itemStyle: {
                                    areaColor: "#f59a05"
                                }
                            },
                            data:[...this.data]
                        },
                        lineseries[0],
                        lineseries[1]
                    ]
                };
                this.chart.setOption(options, true);
                this.chart.on('click', (param)=>{
                    cityName = param.name;
                    this.$emit('showCityName',cityName);
                    var lineseries = [];
                    lineseries.push({
                    name: cityName,
                    type: 'lines',
                    zlevel: 1,
                    show: true,
                    effect: {
                        show: true,
                        period: 4, // 
                        symbol: 'arrow',
                        trailLength: 0.6, //  0  1  0.2
                        color: '#fff',
                        // symbol: planePath, // 
                        symbolSize: 6
                    },
                    tooltip: {
                        show: true, // 
                        trigger: 'item', //   'item' 'axis''none'
                        // backgroundColor: 'rgba(50,50,50,0.7)', // 
                        // borderColor: '#fff', // 
                        borderWidth: 0, // 
                        // padding: 5, // 
                        // textStyle: { // 
                        //     color: '#fff',
                        //     fontStyle: 'normal',
                        //     fontWeight: 'normal',
                        //     fontFamily: 'sans-serif',
                        //     fontSize: 14,
                        // },
                        // extraCssText: 'box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);', //  css 
                        confine: false, //  tooltip 
                        // formatter: '{b}  {c}'
                        formatter: function(arg) {
                            return ''+arg.data.fromName + '' + arg.data.toName + '<br/>:<br/>'+ arg.data.value
                        }
                    },
                    lineStyle: {
                        normal: {
                            color: function(arg){
                                var fromName = arg.data.fromName
                                if (fromName == cityName){
                                    return '#CD5C5C'
                                }
                                return '#3f4b51'},
                            width:  2.5,
                            opacity: 0.2,
                            curveness: 0.2
                        }
                    },
                    data: lines_data[cityName]
                },
                {
                    name: cityName+'2',
                    type: 'effectScatter',      // 
                    coordinateSystem: 'geo',    // 
                    zlevel: 2,
                    rippleEffect: {
                        brushType: 'stroke'     // 
                    },
                    label: {
                        normal: {
                            show: true,
                            position: 'left',  // 
                            
                            textStyle:{
                                fontSize:12,
                            },
                            formatter: '{b}'    // b
                        }
                    },
                    symbolSize: function (data) {
                        if(data[2]==1){
                            return 6
                        }
                        return 3;      // 
                    },
                    itemStyle: {
                        normal: {
                            color: function (val) {
                                if(val.data.name == cityName){
                                    return '#FFA500'
                                }
                                return 'MidnightBlue'}
                        }
                    },
                    // item[1] ==> BJData[[{name:''}, {name:'',value:95}],...]
                    data: link_coordinate[cityName]
                },
                );
                // 
                let options = {
                    title: {
                        text: '2020GDP',
                        subtext: '',
                        textStyle:{
                            color:'#377ee5'
                        },
                        subtextStyle:{
                            color:'#c7d5d9'
                        }
                    },
                    tooltip: {
                        textStyle: {
                            fontSize: '14px',
                        },
                        formatter: function(data){
                            if(!isNaN(data.value)){
                                return data.name+"<br/>"+data.value+"";
                            }else{
                                return data.name
                            }
                        }
                    },
                    geo:{
                        type: "map",
                        roam: false, //
                        map: "china",
                        itemStyle: {
                            areaColor: "LightGrey",
                            borderColor: "#739af5",
                            borderWidth: 1
                        },
                        emphasis: {
                            label: {
                                show: false
                            },
                            itemStyle: {
                                areaColor: "#f59a05"
                            }
                        }
                    },
                    layoutCenter: ['50%', '50%'],//
	                layoutSize:'100%',//
                    visualMap: [
                    {
                        orient: 'horizontal',
                        min: 1900,
                        max: 120000,
                        text: ['GDP()'],
                        realtime: false,
                        calculable: true,
                        bottom:"0%",                              //,'20%'
                        inRange: {
                            color: ['Azure', '#1f8bcb']
                        },
                        textStyle:{
                            color:'#000000'
                        },
                    }],
                    series: [
                        {
                            name:'2020GDP',
                            type: "map",
                            // roam: true,
                            map: "china",   
                            geoIndex: 0,
                            itemStyle: {
                                areaColor: "#1f8bcb",
                                borderColor: "#739af5",
                                borderWidth: 1
                            },
                            emphasis: {
                                label: {
                                    show: false
                                },
                                itemStyle: {
                                    areaColor: "#f59a05"
                                }
                            },
                            data:[...this.data]
                        },
                        lineseries[0],
                        lineseries[1],
                        lineseries[2]
                    ]
                };
                    this.chart.clear();
                    this.chart.setOption(options)
                });
            },
            // 
            startInterval() {
                if (this.intervalId !== null) {
                    clearInterval(this.intervalId);
                }
                this.intervalId = setInterval(() => {
                    this.reSelectMapRandomArea();
                }, 2000);
            },
            // 
            reSelectMapRandomArea() {
                this.$nextTick(() => {
                    let index = Math.floor(Math.random() * this.data.length);
                    this.chart.dispatchAction({
                        type: 'showTip',
                        seriesIndex: 0,
                        dataIndex: index,
                    });
                    this.chart.dispatchAction({
                        type: 'select',
                        seriesIndex: 0,
                        dataIndex: index,
                    });
                });
            },
            handleMapRandomSelect() {
                this.$nextTick(() => {
                    setTimeout(() => {
                        this.reSelectMapRandomArea();
                    }, 0);
                    // 
                    this.chart.on('mouseover', (params)=> {
                        clearInterval(this.intervalId);
                    });
                    // 
                    this.chart.on('globalout', ()=> {
                        this.reSelectMapRandomArea();
                        this.startInterval();
                    });
                    this.startInterval();
                });
            },
        }
    }
</script>

<style scoped>
    .echart {
        height: 12rem;
    }
</style>
